//  vuex .
import Store from "../store/index";

// .
// :
// /router/index.js
// /components/RecipientList.vue
// /view/ShoppingCart.vue

// .
let timer = null;

function loaded() {
  // .
  clearTimeout(timer);

  // .
  Store.commit("LOADED_MUTATIONS");
}

export default {
  // .
  isLoading() {
    // .
    clearTimeout(timer);
    //  500 .
    timer = setTimeout(loaded, Store.state.loadingTime);

    // .
    Store.commit("LOADING_MUTATIONS");
  },

  // .
  isLoaded() {
    // .
    clearTimeout(timer);

    // .
    Store.commit("LOADED_MUTATIONS");
  },
};
